/*-------------------------*/
/* $Globel */
/*-------------------------*/
*, *:before, *:after {
  position: relative;
  box-sizing: border-box; }

html, body {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif', '微軟正黑體', 'Microsoft JhengHei'; }

body {
  background-color: #fff; }

a {
  text-decoration: none; }
  a:hover, a:active, a:focus {
    text-decoration: none;
    outline: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

input, select, textarea {
  border: none; }
  input:focus, select:focus, textarea:focus {
    outline: none; }

img {
  max-width: 100%;
