/*-------------------------*/
/* $Layout */
/*-------------------------*/
.grid-container {
  display: grid;
  width: 960px;
  height: 2000px;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(20, 100px);
  margin: 32px auto; }

/*-------------------------*/
/* $Text */
/*-------------------------*/
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

/*-------------------------*/
/* $Button */
/*-------------------------*/
.btn {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  height: 36px;
  line-height: 36px;
  padding: 0 38px 0 16px;
  box-sizing: border-box;
  font-size: 16px;
  text-align: center;
  font-family: 'Roboto', arial, sans-serif;
  letter-spacing: 0;
  text-decoration: none;
  color: #000000;
  border: 1px solid #000000;
  border-radius: 18px;
  outline: 0;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  user-drag: none;
  transition: all 0.3s; }

/*-------------------------*/
/* $Title */
/*-------------------------*/
.title {
  grid-column: 1/-1;
  grid-row: 1/span 1;
  text-align: center; }
  .title span {
    display: inline-block;
    font-weight: 700;
    font-size: 20px;
    height: 27px;
    line-height: 27px;
    padding: 0 10px;
    background-color: #000000;
    color: #FFFFFF; }
  .title h2 {
    font-weight: 900;
    font-size: 72px;
    color: #000000; }

/*-------------------------*/
